@import '../../../../variables';

.info-bar {
  z-index: 1;
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: #add8e6;

  button {
    margin-bottom: 10px;
    border: none;
    background-color: transparent;
    font-size: 18px;
    outline: none;
    color: #000;

    &:hover {
      cursor: pointer;
    }
  }

  &.medium-and-larger {
    display: none;
  }

  @media screen and (min-width: $medium-breakpoint) {
    display: none;

    &.medium-and-larger {
      width: $info-bar-width-lg-screen;
      display: flex;
    }
  }
}